.practice-form {
  font-size: 1.2rem;
  margin-bottom: 30px;
  border-top: 3px solid var(--light-blue);
  border-bottom: 3px solid var(--light-blue);
  padding: 30px;
  -webkit-animation: slideDown .3s;
  animation: slideDown .3s;
}

.practice-form div {
  display: flex;
  justify-content: space-evenly;
}

.practice-form input {
  display: flex;
  margin: 2px 0 10px 0;
  height: 25px;
  border: 1px solid var(--dark-blue);
  transition: .3s;
}

.practice-form input:focus {
  outline: none;
  border: 1px solid var(--light-blue);
  transition: .3s;
}

.employment-type {
  display: flex;
  flex-direction: column;
}

#employment-type {
  width: 120px;
}
